import { KEYBOARD_LAYOUT } from '@/interfaces';
import { Keycap } from './keycap';
import { clipImage } from '@/utils';


type KeyboardLayoutProps = {
  color?: string
  image?: string;
  type: 'color' | 'image';
}

export const KeyboardLayout = ({
  color,
  image,
  type,
}: KeyboardLayoutProps) => {
  console.log('image is ', image);
  console.log('colr ', type)

  return <div className="flex flex-col gap-1 w-[650px] ml-5">
    {KEYBOARD_LAYOUT.map((row, rowIndex) => (
    <div key={rowIndex} className={`w-full justify-between flex gap-1${rowIndex === 0 ? ' mb-1' : ''}`}>
      {row.map((key, idx) => (
        <Keycap
          type={type}
          backgroundColor={color}
          shiftKey={key.shiftKey}
          key={idx}
          keyChar={key.key}
          imageUrl=''
            imageStyle={type == 'image' && image ? clipImage({
              imageUrl: image,
              xPos: '1',
              yPos: '1',
              keycapWidth: key.width || 'w-[40px]'
            }) : undefined}
          keyWidth={key.width}
        />
      ))}
    </div>
    ))}
  </div>
}
