"use client";
import { Customizer, KeyboardLayout } from '@/components'
import { useKeyboardStyleApplier } from '@/hooks';
import { useState } from 'react';

export default function Home() {
  const [type, setType] = useState<'color' | 'image'>('color');
  const {
    color,
    setColor,
    image,
    setImage,
  } = useKeyboardStyleApplier();

  console.log('image is ', image)

  return (
    <div className="mt-6 flex justify-between w-10/12 mx-auto">
      <Customizer
        color={color}
        colorChange={setColor}
        image={image}
        imageChange={setImage}
        setType={setType}
      />
      <KeyboardLayout color={color} image={image} type={type}/>
    </div>
  );
}
